.App {
  text-align: center;
}

.App-body {
  /* background-color: #d2f5f2; */
  /* background-color: #28341d; */
  background-color: #A899BA;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.modalDialog {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
	right: 0;
	bottom: 0;
	left: 0;
  background: rgba(0,0,0,0.8);
	z-index: 99999;
  opacity:100;
}

.modal-content {
  top: 25vh;
  width:40vh;
  height:auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background: white;
  border-radius: 2%;
}

.modal-header {
  /* align-self: flex-start; */
  height: auto;
  width: 100%;
  margin-bottom: 5%;
  margin-top: 8%;
  color: black;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
}

.modal-text {
  height: auto;
  width: 80%;
  color: black;
  text-align: left;
  font-size: calc(8px + 1vmin);
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-style: normal;
  margin-bottom: 8%;
}

.modal-footer {
  background-color: lightblue;
  width: auto;
  padding: 3%;
  font-size: calc(10px + 1vmin);
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  border-radius: 5% 5%;
  margin-bottom: 5%;
  padding-right: 5%;
  justify-content: center;
  flex-wrap: nowrap;
  white-space:nowrap;
}

.beer-icon {
  padding-right: 5%;
  justify-content: center;
}

.modalDialoginvisible {
  opacity:0;
  display:none;
}

.bar-sign{
  width:100%;
  /* height:40vh; */
  object-fit: cover;
  /* margin-bottom: 3vh; */
  filter:saturate(100%)
}

.bar-signlit{
  filter:saturate(100%);
  width:100%;
  object-fit: cover;
}

/* http://thenewcode.com/438/CSS-Border-Image-Explained */

.bingo-board{
  background-image: url('./assets/coaster-texture.jpg');
  background-color: beige;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 40vh;
  height: 40vh;
  padding: 3%;
  border-radius: 2%;
  box-shadow: 5px 5px 2px 1px darkslategrey;
}

.bingo-square{
  height: 20%;
  width: 20%;
  align-items: center;
  justify-content: center;
  border-image: url('./assets/drawn-outline-01.png') 93 92 87 92 stretch;
  border-width: 6px;
  border-style: dashed;
}

.bingo-squaredone{
  height: 20%;
  width: 20%;
  align-items: center;
  justify-content: center;
  border-image: url('./assets/drawn-outline-01.png') 93 92 87 92 stretch;
  border-width: 6px;
  border-style: dashed;
  background-color: red;
  filter:saturate(40%);
  opacity: 80%;
}

.bingo-squaredone > .bingo-text{
  color:white;
  font-weight: 300;
}

.bingo-text{
  font-size: calc(4px + 2vmin);
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  font-style: normal;
  color: black;
  display: inline-block;
  /* word-break: break-all;  */
  width: 100%;
  height: 100%;
  margin-top: 10;
}

.script{
  font-size: calc(11px + 2vmin);
  font-family: brush-script-std,sans-serif;
  font-weight: 200;
  font-style: normal;
  color: black;
  margin-top: 5vh;
}

.footer-text{
  font-size: calc(4px + 2vmin);
  font-family: 'Roboto Mono', monospace;
  font-weight: 500;
  font-style: normal;
  color: black;
  display: inline-block;
  /* word-break: break-all;  */
  width: 100%;
  height: 100%;
  
}

.footer-text-image{
  font-size: calc(2px + 2vmin);
  font-family: 'Roboto Mono', monospace;
  font-weight: 300;
  font-style: normal;
  text-align: left;
  color: black;
  display: inline-block;
  /* word-break: break-all;  */
  width: 90%;
  height: 90%;
  margin: 3%;
}

.can-image{
  width:100%;
  object-fit: cover;
  border-radius: 2% 2% 0% 0%;

}



@media only screen and (min-device-width: 400px){
  .bar-sign{
    width:40vh;
    height:40vh;
    object-fit: cover;
    /* margin-bottom: 3vh; */
    filter:saturate(100%)}

    .bar-signlit{
      filter:saturate(100%);
      width:40vh;
      object-fit: cover;
    }
  }